.title {
  font-size: 3rem;
  text-align: center;
  font-weight: bold;
  color: var(--primary-color-lighter)};
  @media screen and (prefers-color-scheme: dark) {
    .title {
      color: white; } }

.wrapping-img {
  width: 33%;
  margin-top: 40px;
  margin-bottom: 20px;
  min-width: 400px;
  max-width: 500px;
}

body, html {
  scroll-behavior: smooth;
  @media screen and (prefers-color-scheme: dark) {
    background-color: $dark-secondary-color;
  }
}

a {
  transition: opacity .8s ease-in-out; 
}

a:hover {
  opacity: 0.5;
  cursor: pointer; 
  transition: opacity .8s ease-in-out; }

a.anchor {
  display: block;
  position: relative;
  top: -67px;
  visibility: hidden; }

a {
  color:var(--primary-color);
  text-decoration: none;
  transition: all .2s ease-in-out; }
  
::-moz-selection {
  background: var(--primary-color);
}

::selection {
  background: var(--primary-color);
}

::-moz-selection {
  color: white;
}

::selection {
  color: white;
}

.page {
  margin-left: 5vw;
  margin-right: 5vw;
}

.section {
  margin-top: 50px;
  margin-bottom: 50px;
}

p {
  font-size: 1.15rem;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  background-color: lightgrey;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--primary-color-lighter); 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}

@media (max-width: 810px) {
  .wrapping-img{
    width: 70%;
    display: block;
    float: none;
    margin: auto;
    margin-bottom: 20px;
    min-width: 100%;
  }}

@media (prefers-reduced-motion: no-preference) {
  .animation-cool {
    animation: App-logo-spin infinite 20s linear;
  }
}
